.Navbar {
    border-radius: 5px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background: rgb(11, 11, 11);
    background: linear-gradient(90deg, rgba(11, 11, 11, 1) 0%, rgba(31, 31, 31, 1) 82%, rgba(55, 55, 55, 1) 100%);
    border-bottom: 1.5px solid #070707;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.Navbar a {
    text-decoration: none;
    color: whitesmoke;
    font-size: large;
}

.Navbar a:hover {
    color: rgba(34, 255, 0, 0.3);
}

.Navbar ul {
    margin: 5px 5px 5px 0px;
}

.Navbar li {
    padding-left: 5px;
    padding-right: 5px;
    display: inline-block;
}

.Navbar-logo {
    position: absolute;
    height: 100px;
    top: 5px;
    left: 5px;
    border-radius: 10px;
    border: 1.5px solid #070707;
}