.AboutStaff {
    background-color: rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(0.5rem);
    backdrop-filter: blur(0.75rem);
    padding: 10px 10px 10px 10px;
    border-radius: 5px;
    background-size: cover;
    text-align: center;
    margin-left: 15vw;
    margin-right: 15vw;
    margin-bottom: 20px;
    border: 1.5px solid #070707;
    text-align: justify;
}

.about-them {
    background-color: transparent;
    -webkit-backdrop-filter: blur(0rem);
    backdrop-filter: blur(0rem);
    margin: 0;
    padding-top: 10px;
    min-height: 100px;
}

.profile-pic {
    width: 120px;
    height: 150px;
    border-radius: 50%;
    border: #212121 solid 4px;
    text-align: center;
    float: left;
    shape-outside: circle();
    margin: 0 20px 20px 0;
    z-index: 5000;
}

.name {
    font-size: 20px;
    font-weight: 1000;
    margin: 5px 0 0 0;
    padding: 0;
}

.role {
    font-size: 15px;
    font-weight: 700;
    margin: 5px 0 0 0;
}